import { createContext, useContext, useState } from "react";

const ModeContext = createContext();

export const Mode = {
  BASIC: {
    id: 'BASIC',
    label: "Basic",
    imgSource: "/basic-mode.png"
  },
  EXAMPLES: {
    id: 'EXAMPLE',
    label: "Examples",
    imgSource: "/examples-mode.png"
  },
  ADVANCED: {
    id: 'ADVANCED',
    label: "Advanced",
    imgSource: "/advanced-mode.png"
  },
};

export function ModeProvider({ children }) {
  const [mode, setMode] = useState(Mode.BASIC);

  return (
    <ModeContext.Provider value={{ mode, setMode }}>
      {children}
    </ModeContext.Provider>
  );
}

export function useMode() {
  return useContext(ModeContext);
}

export function getModeFromId(modeId){
  let newMode = undefined;
  for (const mode of Object.values(Mode)){
    if (mode.id === modeId){
      newMode = mode;
      break;
    }
  }
  return newMode;
}

export function isBasicMode(mode) {
  return mode === Mode.BASIC;
}

export function isExamplesMode(mode) {
  return mode === Mode.EXAMPLES;
}

export function isAdvancedMode(mode) {
  return mode === Mode.ADVANCED;
}